<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar
      class="page-nav-bar"
      title=""
      left-arrow
      @click-left="$router.back()"
    />
    <!-- / -->

    <!--  -->
    <input
      type="file"
      hidden
      ref="file"
      @change="onFileChange()"
    />

    <!--  -->
    <van-cell title="" class="photo-cell" center is-link @click="$refs.file.click()">
      <van-image
        class="avatar"
        fit="cover"
        round
        :src="user.photo"
      />
    </van-cell>
    <van-cell title="" :value="user.name" is-link @click="isUpdateNameShow = true"/>
    <van-cell title="" :value="user.gender === 0 ? '' : ''" is-link @click="isUpdateGenderShow = true"/>
    <van-cell title="" :value="user.birthday" is-link @click="isUpdateBirthdayShow = true"/>
    <!-- / -->

    <!--  -->
    <van-popup
      v-model="isUpdateNameShow"
      style="height: 100%;"
      position="bottom"
    >
      <updata-name v-if="isUpdateNameShow" v-model="user.name" @close="isUpdateNameShow = false"></updata-name>
    </van-popup>
    <!-- / -->

    <!--  -->
    <van-popup
      v-model="isUpdateGenderShow"
      position="bottom"
    >
      <updata-gender
        v-if="isUpdateGenderShow"
        @close="isUpdateGenderShow = false"
        v-model="user.gender"
      />
    </van-popup>
    <!-- / -->

    <!--  -->
    <van-popup
      v-model="isUpdateBirthdayShow"
      position="bottom"
    >
      <updata-birthday
        v-if="isUpdateBirthdayShow"
        v-model="user.birthday"
        @close="isUpdateBirthdayShow = false"
      />
    </van-popup>
    <!-- / -->

    <!--  -->
    <van-popup
      v-model="isUpdatePhotoShow"
      style="height: 100%"
      position="bottom"
    >
      <updata-photo
        v-if="isUpdatePhotoShow"
        :img="img"
        @close="isUpdatePhotoShow = false"
        @update-photo="user.photo = $event"
      ></updata-photo>
    </van-popup>
    <!--  -->
  </div>
</template>

<script>
import { getUserProfileAPI } from '../../api/index.js'
import UpdataName from './components/updata-name.vue'
import UpdataGender from './components/updata-gender.vue'
import UpdataBirthday from './components/updata-birthday.vue'
import UpdataPhoto from './components/updata-photo.vue'
export default {
  components: {
    UpdataName,
    UpdataGender,
    UpdataBirthday,
    UpdataPhoto
  },
  data () {
    return {
      user: {}, // 
      isUpdateNameShow: false, // 
      isUpdateGenderShow: false, // 
      isUpdateBirthdayShow: false, // 
      img: null, // 
      isUpdatePhotoShow: false // 
    }
  },

  created () {
    // 
    this.loadProfile()
  },

  methods: {
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
      } catch (err) {
        this.$toast('')
      }
    },

    onFileChange () {
      // 
      const file = this.$refs.file.files[0]

      //  blob 
      this.img = window.URL.createObjectURL(file)

      // 
      this.isUpdatePhotoShow = true

      // file-input  change 
      //  value 
      this.$refs.file.value = ''
    }
  }
}
</script>

<style scoped lang='less'>
.user-profile {
  .avatar {
    width: 60px;
    height: 60px;
  }

  .van-popup {
    background: #f5f7f9;
  }

  .photo-cell {
    .van-cell__value {
      display: flex;
      flex-direction: row-reverse;
    }
  }
}
</style>
