<template>
  <div class="updata-name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm()"
    />
    <!-- / -->

    <!--  -->
    <div class="field-wrap">
      <van-field
        v-model.trim="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="10"
        placeholder=""
        show-word-limit
      />
    </div>
    <!-- / -->
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdataName',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localName: this.value
    }
  },

  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // 
      })

      try {
        const localName = this.localName
        if (!localName.length) {
          this.$toast('')
          return
        }

        await updateUserProfileAPI({
          name: localName
        })

        // 
        this.$emit('input', localName)

        // 
        this.$emit('close')

        // 
        this.$toast.success('')
      } catch (err) {
        console.dir(err)
        if (err.response && err.response.status === 409) {
          return this.$toast('')
        }
        this.$toast('')
      }
    }
  }
}
</script>

<style scoped lang='less'>
.field-wrap {
  padding: 20px;
}

</style>
