<template>
  <div class="update-gender">
    <van-picker
      show-toolbar
      title=""
      :columns="columns"
      :default-index="value"
      @cancel="$emit('close')"
      @change="onChange"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      columns: ['', ''],
      localGender: this.value
    }
  },
  methods: {
    onChange (picker, value, index) {
      this.localGender = index
    },

    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // 
      })

      try {
        const localGender = this.localGender

        await updateUserProfileAPI({
          gender: localGender
        })

        // 
        this.$emit('input', localGender)

        // 
        this.$emit('close')

        // 
        this.$toast.success('')
      } catch (err) {
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style scoped lang="less"></style>
